@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600);
body {
  background: #e2e2e2;
  background: -moz-linear-gradient(top, #e2e2e2 0%, #dbdbdb 39%, #d1d1d1 51%, #fefefe 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, #e2e2e2), color-stop(39%, #dbdbdb), color-stop(51%, #d1d1d1), color-stop(100%, #fefefe));
  background: -webkit-linear-gradient(top, #e2e2e2 0%, #dbdbdb 39%, #d1d1d1 51%, #fefefe 100%);
  background: -o-linear-gradient(top, #e2e2e2 0%, #dbdbdb 39%, #d1d1d1 51%, #fefefe 100%);
  background: -ms-linear-gradient(top, #e2e2e2 0%, #dbdbdb 39%, #d1d1d1 51%, #fefefe 100%);
  background: linear-gradient(to bottom, #e2e2e2 0%, #dbdbdb 39%, #d1d1d1 51%, #fefefe 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e2e2e2', endColorstr='#fefefe', GradientType=0 );
}

.container-fluid {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.menu-section {
  display: flex;
  flex: 1 0 auto;
}

.img-menu img {
  width: 105%;
  height: auto;
  background-size: cover;
  position: relative;
}

#single-nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 15px;
}

#single-nav ul {
  justify-content: flex-start;
  display: flex;
  list-style: none;
  text-decoration: none;
}

#single-nav ul li {
  margin-right: 5px;
}

#single-nav ul li a {
  margin-right: 10px;
  color: #115c5f;
  border-right: 1px solid #256739;
  padding-right: 5px;
}

#single-nav .redes-soc {
  display: flex;
  flex-wrap: wrap;
  margin-right: 20px;
  flex: 1 1 auto;
  justify-content: flex-end;
}

#single-nav .redes-soc img {
  width: 2.8rem;
  height: 2.8rem;
  margin-left: 5px;
  transition: all 1s ease;
}

#single-nav .redes-soc img:hover {
  transform: rotateY(360deg);
  transform-style: preserve-3d;
}

/*////////////////////menu-principal////////////////*/
#menu-principal {
  background-color: #29375c;
  display: flex;
  justify-content: space-around;
}

#menu-principal ul {
  margin-top: 5px;
  justify-content: center;
  flex-wrap: wrap;
  display: flex;
  align-items: center;
  list-style: none;
}

#menu-principal ul li {
  align-items: center;
  flex: 1 1 auto;
}

#menu-principal ul li a {
  margin-right: 20px;
  color: #fff;
  align-items: center;
  border-right: 1px solid #fff;
  padding-right: 17px;
}

/*///////////////menu footer/////////////*/
#menu-footer {
  margin-top: 15px;
  background-color: #29375c;
  display: flex;
  justify-content: space-around;
}

#menu-footer ul {
  flex-wrap: wrap;
  display: flex;
  align-items: center;
  list-style: none;
  justify-content: space-between;
}

#menu-footer ul li {
  align-items: center;
}

#menu-footer ul li a {
  margin-right: 5px;
  color: #fff;
  align-items: center;
  border-right: 1px solid #fff;
  padding-right: 15px;
}

#menu-footer ul li a:hover {
  color: #266539;
  text-decoration: none;
}

/*//////////////////home page////////////////////////*/
#reg .btn-success {
  /* color: #fff; */
  background-color: #266539;
  border-color: #4cae4c;
  width: 200px;
  margin-top: 5px;
  margin-left: 180px;
}

#menu-icono {
  margin-top: 50px;
  width: 100%;
}

#menu-icono ul {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

#menu-icono ul li {
  list-style: none;
}

#menu-icono ul li a {
  background-color: #29375c;
  width: 10rem;
  padding: 0;
  border: 1px solid #b6b8b9;
  box-shadow: 5px 5px 10px #999;
}

#menu-icono ul li a:hover {
  background-color: white;
  border: 1px solid #28653c;
}

#menu-icono ul li h6 {
  margin-top: 4px;
  color: #29375c;
  font-size: 15px;
}

#menu-icono ul li a img {
  width: 50px;
}

#menu-lateral {
  justify-content: space-between;
  display: flex;
  flex-wrap: wrap;
}

#menu-lateral .btn-success {
  margin: 0;
  padding: 0;
  background-color: #29375c;
  box-shadow: 4px 4px 7px #29375c;
  width: 170px;
  height: 60px;
  align-items: center;
  display: flex;
  justify-content: space-between;
  border: 1px solid #b6b8b9;
  margin-top: 7px;
}

#menu-lateral ul {
  list-style: none;
}

#menu-lateral .btn-success:hover {
  color: #28653c;
  background-color: #fff;
  border: 1px solid #b6b8b9;
  box-shadow: 4px 4px 7px #28653c;
}

#menu-lateral .gente img {
  width: 160px;
  height: 140px;
  margin-left: 5px;
  margin-top: 40px;
  box-shadow: 4px 4px 7px #999;
}

#menu-lateral ul li a.btn-success img {
  width: 35px;
  height: 35px;
}

/*///////////media querys/////////////*/
header {
  width: 100%;
  margin: 0;
}

header nav {
  width: 100%;
  background: #29375c;
}

.menu_bar {
  display: none;
}

header nav ul {
  display: flex;
  justify-content: space-between;
  list-style: none;
}

header nav ul li {
  float: left;
  padding-bottom: 10px;
  padding-top: 10px;
}

header nav ul li a {
  color: #fff;
  border-right: 1px solid #fff;
  padding-right: 30px;
  display: block;
  text-decoration: none;
}

header nav ul li span {
  margin-right: 10px;
}

header nav ul li a:hover {
  color: #266539;
  text-decoration: none;
}

@media screen and (max-width: 800px) {
  .menu-section {
    display: flex;
    flex: 1 1 auto;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  header nav {
    width: 100%;
    height: 100%;
    left: -100%;
    margin: 0;
    position: absolute;
  }
  header nav ul {
    display: block;
    justify-content: space-between;
    flex-direction: row;
    list-style: none;
  }
  header nav ul li {
    display: block;
    float: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  }
  .menu_bar {
    display: block;
    width: 100%;
    background: #ccc;
  }
  .menu_bar .bt-menu {
    display: block;
    padding: 20px;
    background: #29375c;
    color: #fff;
    text-decoration: none;
    font-weight: bold;
    font-size: 25px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  #videos {
    flex-direction: row;
  }
}

